<template>
  <div class="container">
    <main class="main">
      <h1 class="title">
        Волонтерська <a>Платформа</a>
      </h1>

      <p v-if="tab==0" class="description">
        Будуємо горизонтальні зв'язки та встановлюємо ефективну комунікацію між волонтерами, волонтерськими групами та
        громадськими організаціями.
      </p>

      <div class="grid">
        <button class="button" @click="tab=0">Про нас</button>
        <button class="button" @click="tab=1">Проекти</button>
        <button class="button" @click="tab=2">Учасники</button>
        <button class="button" @click="tab=3">{{ auth == 0 ? "Увійти" : name }}</button>
      </div>

      <div v-if="tab==0" class="grid">
        <p class="description2">
          У кожного народжуються геніальні ідеї. Деякі ми можемо реалізувати самостійно, а часом для того, щоб проект
          змінив Світ, нам потрібна допомога. І ми - шукаємо волонтерів. Ми пишемо про це у соцмережах, запитуємо у
          друзів, чи мають вони вільний час, щоб допомогти? Ми пишемо листи і шукаємо людей, які допоможуть не за гроші,
          а просто через те, що поділяють наші погляди і хочуть, щоб наша ідея отримала шанс життя. Саме для того, щоб
          люди із визначними ідеями і задумами знаходили допомогу, а волонтери мали можливість віднайти проекти, де їхні
          уміння чи час будуть корисними, ми створили платформу volonter.org.
        </p>
        <img src="./assets/19570-1.png" alt="image"/>
      </div>

      <div v-if="tab==1" class="grid">
        <section class="card" v-for="p in projects" :key="p">
          <h2>{{ p["Title"] }}&rarr;</h2>
          <p>{{ p["Content"] }}</p>
          <center><img style="width:100%" :src="p['Image']" :alt="p['Title']"/></center>
        </section>
      </div>

      <div v-if="tab==2" class="grid">
        <section class="card" v-for="u in users" :key="u">
          <center><h2>{{ u['Name'] }}</h2></center>
          <center><img style="width:80%" :src="u['Avatar']" :alt="u['Name']"/></center>
          <center><p>{{ u['lastName'] }}</p></center>
        </section>
      </div>

      <section v-if="tab==3 && auth==0" class="description2">
        <input type="text" class="input" placeholder="ім'я" v-model="name"/><br/>
        <input type="password" class="input" placeholder="пароль" v-model="password"/><br/>
        <button type="button" class="button-sm" @click="login()">Увійти</button>
        <button type="button" class="button-sm" @click="tab=4">Не зареєстрований? Зареєструватися</button>
      </section>

      <section v-if="tab==4 && auth==0" class="description2">
        <input type="text" class="input" placeholder="ім'я" v-model="name"/><br/>
        <input type="text" class="input" placeholder="прізвище" v-model="прізвище"/><br/>
        <input type="password" class="input" placeholder="пароль" v-model="password"/><br/>
        <input type="file" accept="image/jpeg" @change="uploadImage" class="input" placeholder="аватар"/><br/>
        <img width="80" v-if="avatar!=''" :src="avatar"/><br/>
        <button type="button" class="button-sm" @click="logup()">Зареєструватися</button>
        <button type="button" class="button-sm" @click="tab=3">Зареєстрований? Увійти</button>
      </section>

      <p v-if="tab==3 && user['Status']=='user'">
        <button type="button" class="button-sm" @click="exit()">Вийти</button>
      </p>

      <p v-if="tab==3 && user['Status']==''">
        <button type="button" class="button-sm" @click="exit()">Вийти</button>
      </p>

