        <b>Привіт адмін {{ name }}</b>
        <ul>
          <li v-for="i in projects" :key="i">
            <span>{{ i["Title"] }}<button @click="del(i)">Del</button></span>
          </li>
        </ul>
        <div style="border-radius:1rem;border: 1px solid lime; pagging:0.5rem; margin:1rem;">
          <input type="text" v-model="product.category" placeholder="категорія" />
          <input type="text" v-model="product.title" placeholder="заголовок" />
          <input type="text" v-model="product.content" placeholder="опис" />
          <input type="text" v-model="product.date" placeholder="дата" />
          <input type="text" v-model="product.place" placeholder="розташування" />
          <input type="file" accept="image/jpeg" @change="uploadImage2" class="input" placeholder="зображення"/><br/>
          <img width="80" v-if="product.image!=''" :src="product.image"/><br/>
          <button type="button" class="button-sm" @click="add()">Додати</button>
        </div>
        <button type="button" class="button-sm" @click="exit()">Вийти</button>
      </section>

