<script>
export default {
  name: 'App',
  components: {},
  data: () => ({
    projects: [],
    users: [],
    tab: 0,
    name: "",
    password: "",
    auth: 0,
    user: {},
    lastName: "",
    avatar: "",
    toggle: false,
    product: {
      category: "",
      title: "",
      content: "",
      image: "",
      date: "",
      place: "",
    },
  }),
  mounted: async function () {
    await this.getData();
  },
  methods: {
    exit() {
      this.user = {};
      this.name = '';
      this.password = '';
      this.auth = 0;
      this.tab = 0;
    },
    async getData() {
      const projects = await fetch("https://localhost:7186/api/projects");
      this.projects = "[" + (await projects.json()) + "]";
      this.projects = JSON.parse(this.projects);

      const users = await fetch("https://localhost:7186/api/users");
      this.users = "[" + (await users.json()) + "]";
      this.users = JSON.parse(this.users);
    },
    uploadImage(e) {
      const image = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(image);
      reader.onload = e => {
        let previewImage = e.target.result;
        console.log(previewImage);
        this.avatar = previewImage;
      };
    },
    uploadImage2(e) {
      const image = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(image);
      reader.onload = e => {
        let previewImage = e.target.result;
        console.log(previewImage);
        this.product.image = previewImage;
      };
    },
    async login() {
      const user = await fetch("https://localhost:7186/api/Users/Auth", {
        method: 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          "status": "user",
          "name": this.name,
          "password": this.password,
          "lastName": "",
          "avatar": "",
          "subscribes": "",
          "id": 0
        }),
      });
      this.user = await user.json();
      this.auth = 1;
    },
    async logup() {
      const data = {
        "status": "user",
        "name": this.name,
        "password": this.password,
        "lastName": this.lastName,
        "avatar": this.avatar,
        "subscribes": "",
        "id": 0
      };
      const user = await fetch("https://localhost:7186/api/Users/Register", {
        method: 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      this.user = await user.json();
      this.auth = 1;
    },
    async del(o) {
      const user = await fetch("https://localhost:7186/api/Projects", {
        method: 'DELETE',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(o),
      });
      await user.text();
      await this.getData();
    },
    async add(){
      const data = {
        "category": this.product.category,
        "title": this.product.title,
        "content": this.product.content,
        "image": this.product.image,
        "date": this.product.date,
        "place": this.product.place,
        "id": 0
      };
      const project = await fetch("https://localhost:7186/api/Projects", {
        method: 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      await project.text();
      await this.getData();
      this.product.category = "";
      this.product.title = "";
      this.product.content = "";
      this.product.image = "";
      this.product.date = "";
      this.product.place = "";
    }
  }
}
</script>

